.navbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: #1F3044;
	padding: 10px;
	border-radius: 5px;
	position: sticky;
	top: 0;
	z-index: 10;
  }
  
  .navbar h1 {
	margin: 0;
	color: #fff;
	font-size: 1.5rem;
  }
  
  .navbar select {
	color: #fff;
	background-color: #272727;
	border: none;
	border-radius: 5px;
	padding: 5px;
	font-size: 1rem;
  }
  
  .navbar label {
	color: #fff;
	margin: 0 10px;
  }
  
  .navbar input[type="range"] {
	background-color: #fff;
	height: 5px;
	border-radius: 5px;
  }
  
  .navbar input[type="range"]::-webkit-slider-thumb {
	width: 15px;
	height: 15px;
	border-radius: 50%;
	background-color: #fff;
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -3px;
  }
  
  .navbar input[type="range"]::-webkit-slider-runnable-track {
	background-color: #1F3044;
	border-radius: 5px;
  }